// InsertCandidateSkills.js

import React, { useState } from 'react';
import { TextField, Card, CardContent, Typography, Button, makeStyles } from '@material-ui/core';
import CandidateSkillModel from '../../dataObjects/CandidateSkillModel'; // Importing the model class

const useStyles = makeStyles((theme) => ({
  card: {
    // margin: theme.spacing(2),
    // padding: theme.spacing(2),
  },
  textField: {
    marginBottom: theme.spacing(2),
  },
  addButton: {
    marginTop: theme.spacing(2),
    marginBottom:theme.spacing(2),
  },
}));

const InsertCandidateSkills = ({ onSkillsChange }) => {
  const classes = useStyles();
  const [skills, setSkills] = useState([new CandidateSkillModel()]); // Initialize with an empty skill

  const handleSkillChange = (index, skillName) => {
    const newSkills = [...skills];
    newSkills[index] = new CandidateSkillModel(skillName);
    setSkills(newSkills);
  };

  const handleAddSkill = () => {
    setSkills([...skills, new CandidateSkillModel()]); // Add a new skill model
  };

  const handleRemoveSkill = (index) => {
    const newSkills = [...skills];
    newSkills.splice(index, 1); // Remove the skill at the specified index
    setSkills(newSkills);
  };

  // Notify parent component of the changes
  React.useEffect(() => {
    onSkillsChange(skills.filter(skill => skill.skillName.trim() !== '')); // Remove empty skills
  }, [skills, onSkillsChange]);

  return (
    <Card className={classes.card}>
      <CardContent>
        <Typography variant="h5" gutterBottom>Candidate Skills</Typography>
        {skills.map((skill, index) => (
          <TextField
            key={index}
            className={classes.textField}
            label={`Skill ${index + 1}`}
            value={skill.skillName}
            onChange={(e) => handleSkillChange(index, e.target.value)}
            variant="outlined"
            fullWidth
            InputProps={{
              endAdornment: (
                <div style={{ display: 'flex', alignItems: 'center' }}>
                  <Button
                    className={classes.addButton}
                    variant="contained"
                    color="primary"
                    onClick={handleAddSkill}
                    disabled={skill.skillName.trim() === ''} // Disable if newSkill is empty
                    style={{ marginRight: '8px' }} // Add margin-right for spacing
                  >
                    Add
                  </Button>
                  {skills.length > 1 && (
                    <Button
                      className={classes.removeButton}
                      variant="contained"
                      color="secondary"
                      onClick={() => handleRemoveSkill(index)}
                    >
                      Remove
                    </Button>
                  )}
                </div>
              ),
            }}
          />
        ))}
      </CardContent>
    </Card>
  );
};

export default InsertCandidateSkills;
