// ContactSection.js
import React from 'react';
import PhoneIcon from '@mui/icons-material/Phone';
import EmailIcon from '@mui/icons-material/Email';

const ContactSection = ({ data }) => {
    if (!data) {
        return null;
    }
    const { emailId, phoneNumber } = data;

    const contactSectionStyles = {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'space-between',
        alignItems: 'center',
        margin: '20px 20px 0px 20px',
        color: 'black'
    };

    const contactSectionItemStyles = {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'start'
    };

    return (
        <div style={contactSectionStyles}>
            <div style={contactSectionItemStyles}>
                <EmailIcon style={{marginRight:'5px' , color : 'white'}}/>
                <a href={`mailto:${emailId}`} style={{ textDecoration: 'underline', color: 'white' }}>{emailId}</a>
            </div>
            <div style={contactSectionItemStyles}>
                <PhoneIcon style={{marginRight:'5px' , color: 'white'}} />
                <a  href={`tel:${phoneNumber}`} style={{ textDecoration: 'underline', color: 'white' }}>{phoneNumber}</a>

            </div>
        </div>
    );
};

export default ContactSection;
