// HeaderSection.js
import React from 'react';
import GitHubIcon from '@mui/icons-material/GitHub';
import LinkIcon from '@mui/icons-material/Link';
import LinkedInIcon from '@mui/icons-material/LinkedIn';

const ExtraPortfolioDetailsSection = ( { data } ) => { // Destructure data directly from props

    // Check if data is null or undefined
    if (!data) {
        return null; // or return a loading indicator or default text
    }
    const portfolioSectionStyle = {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'space-between',
        alignItems: 'center',
        margin: '12px 20px 0px 20px',
        color: 'black'
    };

    const portfolioSectionItemStyle = {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'start'
    };
    
    const {portfolioList} = data 

    return (
        <div style={portfolioSectionStyle}>
            <a href={portfolioList[0]} target="_blank" rel="noopener noreferrer" style={{ textDecoration:'bold', color: 'inherit' }}>
                <div style={portfolioSectionItemStyle}>
                    <GitHubIcon  style={{marginRight : '5px',color:'white'}}/>
                    <span style={{color:'white',textDecorationLine:'underline'}}>{portfolioList[0]}</span>
                </div>
            </a>
            <a href={portfolioList[1]} target="_blank" rel="noopener noreferrer" style={{ textDecoration: 'none', color: 'inherit' }}>
                <div style={portfolioSectionItemStyle}>
                    <LinkedInIcon style={{marginRight:'5px' , color : 'white'}} />
                    <span style={{color:'white',textDecorationLine: 'underline'}}>{portfolioList[1]}</span>
                </div>
            </a>
        </div>
    );
};

export default ExtraPortfolioDetailsSection;
