import React, { useState } from 'react';
import { TextField, Button, Card, CardContent, Typography } from '@material-ui/core';
import CandidateAchievementModel from '../../dataObjects/CandidateAchievementModel.js';

const InsertCandidateAchievements = ({ onAchievementsChange }) => {
  const [achievements, setAchievements] = useState([]);

  const handleAddAchievement = () => {
    const newAchievement = new CandidateAchievementModel('', '');
    setAchievements([...achievements, newAchievement]);
  };

  const handleRemoveAchievement = (index) => {
    const updatedAchievements = [...achievements];
    updatedAchievements.splice(index, 1);
    setAchievements(updatedAchievements);
    onAchievementsChange(updatedAchievements);
  };

  const handleAchievementChange = (index, field, value) => {
    const updatedAchievements = [...achievements];
    updatedAchievements[index][field] = value;
    setAchievements(updatedAchievements);
    onAchievementsChange(updatedAchievements);
  };

  return (
    <Card style={{ marginBottom: '16px' }}>
      <CardContent>
        <Typography variant="h5" gutterBottom>Achievements</Typography>
        {achievements.map((achievement, index) => (
          <div key={index} style={{ marginBottom: '16px', display: 'flex', flexDirection: 'column' }}>
            <TextField
              label="Achievement Heading"
              value={achievement.achievementHeading}
              onChange={(e) => handleAchievementChange(index, 'achievementHeading', e.target.value)}
              variant="outlined"
              fullWidth
              style={{marginTop:'8px', marginBottom: '16px' }}
            />
            <TextField
              label="Achievement Description"
              value={achievement.achievementDescription}
              onChange={(e) => handleAchievementChange(index, 'achievementDescription', e.target.value)}
              variant="outlined"
              inputProps={{ maxLength: 999 }} // Maximum character limit
              fullWidth
              multiline
              rows={4}
              style={{ marginBottom: '8px' }}
            />
            <Button variant="outlined" color="secondary" onClick={() => handleRemoveAchievement(index)}>Remove</Button>
          </div>
        ))}
        <Button variant="contained" color="primary" fullWidth  onClick={handleAddAchievement}>Add Achievement</Button>
      </CardContent>
    </Card>
  );
};

export default InsertCandidateAchievements;
