import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import { TextField, Button, Typography, Box, Container, Paper, CssBaseline, Stepper, Step, StepLabel } from '@material-ui/core';
import SwipeableViews from 'react-swipeable-views';
import { useLocation, useNavigate } from 'react-router-dom';
import InsertBasicDetails from './components/insertDataSections/InsertBasicDetails';
import InsertCandidateEducation from './components/insertDataSections/InsertCandidateEducation';
import InsertCandidateSummary from './components/insertDataSections/InserCandidateSummary';
import InsertCandidateSkills from './components/insertDataSections/InsertCandidateSkills';
import InsertCandidateAchievements from './components/insertDataSections/InsertCandidateAchievement';
import InsertCandidateCommunication from './components/insertDataSections/InsertCandidateCommunication';
import InsertCandidateWorkExp from './components/insertDataSections/InsertCandidateWorkExp';
import ReactGA from './ReactGAnalytics';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  root: {
    minHeight: '100vh',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: '#ffffff',
    padding: theme.spacing(3),
  },
  swipeButton: {
    position: 'absolute',
    top: '50%',
    transform: 'translateY(-50%)',
    zIndex: 1,
    backgroundColor: theme.palette.background.paper,
    color: theme.palette.primary.main,
    '&:hover': {
      backgroundColor: theme.palette.background.paper,
    },
    '& svg': {
      fontSize: '2rem',
    },
    // Style for left button
    left: theme.spacing(2),
    // Style for right button
    right: theme.spacing(2),
  },
  container: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    background: 'linear-gradient(180deg, #ffffff 0%, #f2f2f2 100%)', // Define your gradient background
    padding: theme.spacing(4),
    borderRadius: theme.shape.borderRadius,
    boxShadow: theme.shadows[5],
    width: '100%',
    maxWidth: '800px',
  },
  form: {
    width: '100%',
    marginTop: theme.spacing(3),
    background:'transparent',

  },
  stepper: {
    width: '100%',
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(3),
    backgroundColor: 'transparent',
  },
  buttonContainer: {
    display: 'flex',
    justifyContent: 'center',
    marginTop: theme.spacing(2),
  },
  button: {
    margin: theme.spacing(1),
  },
  card: {
    width: '100%',
  },
}));

const CreateResumePage = () => {
  const classes = useStyles();
  const location = useLocation();
  const navigate = useNavigate();
  const { resumeId } = location.state;
  const [cardSectionHeights, setCardSectionHeights] = useState([]);
  const [swipeDisabled,setSwipeDisabled] = useState(true);
  const containerRef = useRef(null);
  const [resumeData, setResumeData] = useState({
    resumeId: resumeId,
    candidateBasicDetails: {},
    candidateEducation: {},
    candidateSummaryEntity: {},
    candidateSkills: [],
    candidateAchievements: [],
    candidateCommunications: [],
    candidateWorkExp: []
  });
  const [currentStep, setCurrentStep] = useState(0);
  const [isStepValid, setIsStepValid] = useState(false);

  useEffect(() => {
    ReactGA.send({ hitType: "pageview", page: "Create Resume Page", title: "Create Resume Page Viewed" });
    axios.get(`https://backendresume-xqwm.onrender.com/api/resume/${resumeId}`)
      .then(response => {
        if (response.status === 200 && response.data) {
          alert('Resume ID already exists. Redirecting back...');
          navigate(-1);
        }
      })
      .catch(error => {
        if (error.response && error.response.status === 404) {
          console.log('Resume ID not found. Proceeding with creation.');
        } else {
          console.error('Error fetching resume data:', error);
          alert('Error checking resume ID. Redirecting back...');
          navigate(-1);
        }
      });
  }, [resumeId, navigate]);

  const handleBasicDetailsChange = (basicDetails) => {
    setResumeData(prevState => ({ ...prevState, candidateBasicDetails: basicDetails }));
    validateStep(currentStep, { ...resumeData, candidateBasicDetails: basicDetails });
  };

  const handleCandidateEducationChange = (education) => {
    setResumeData(prevState => ({ ...prevState, candidateEducation: education }));
    validateStep(currentStep, { ...resumeData, candidateEducation: education });
  };

  const handleCandidateSummaryChange = (summary) => {
    setResumeData(prevState => ({ ...prevState, candidateSummaryEntity: summary }));
    validateStep(currentStep, { ...resumeData, candidateSummaryEntity: summary });
  };

  const handleSkillsChange = (skillsData) => {
    setResumeData((prevState) => ({ ...prevState, candidateSkills: skillsData }));
    validateStep(currentStep, { ...resumeData, candidateSkills: skillsData });
  };

  const handleCandidateAchievementsChange = (achievements) => {
    setResumeData((prevState) => ({ ...prevState, candidateAchievements: achievements }));
    validateStep(currentStep, { ...resumeData, candidateAchievements: achievements });
  };

  const handleCommunicationChange = (communications) => {
    setResumeData((prevState) => ({ ...prevState, candidateCommunications: communications }));
    validateStep(currentStep, { ...resumeData, candidateCommunications: communications });
  };

  const handleWorkExperienceChange = (workExperiences) => {
    setResumeData((prevState) => ({ ...prevState, candidateWorkExp: workExperiences }));
    validateStep(currentStep, { ...resumeData, candidateWorkExp: workExperiences });
  };

  const handleNext = () => {
    if (containerRef.current) {
      containerRef.current.scrollIntoView({ behavior: 'smooth', block: 'start' });
    }
    setCurrentStep((prevStep) => Math.min(prevStep + 1, 6));
  };

  const handleBack = () => {
    setCurrentStep((prevStep) => Math.max(prevStep - 1, 0));
  };

  const handleSubmit = () => {
    ReactGA.event({
      category: 'Button Click',
      action: 'Submit Resume Clicked',
      label: 'Create Resume Page',
    });
    setResumeData((prevState) => ({ ...prevState, resumeId: resumeId }));
    axios.post(`https://backendresume-xqwm.onrender.com/api/resume/new/${resumeId}`, resumeData)
      .then(response => {
        console.log('Resume created successfully:', response.data);
        alert(`Resume created successfully! Please take note of your unique Resume ID: \n\n🆔 ${response.data.resumeId}\n\nYou'll need this ID to access your resume in the future.`);
        navigate(-1);
      })
      .catch(error => {
        console.error('Error creating resume:', error);
        alert('Failed to create resume. Please try again.');
      });
  };

  useEffect(() => {
    if (containerRef.current) {
      containerRef.current.scrollIntoView({ behavior: 'smooth', block: 'start' });
    }
    validateStep(currentStep);
    if(currentStep===6){
      setSwipeDisabled(false);
    }
    ReactGA.send({ hitType: "pageview", page: `Widget Viewed${steps[currentStep]}` , title: 'Current Widget Viewed' , label :"Current Widget Viewed" });
  }, [currentStep]);

  const validateStep = (step, data = resumeData) => {
    let isValid = false;
    switch (step) {
      case 0:
        isValid = validateAllFieldsFilled(data.candidateBasicDetails) && data.candidateBasicDetails.portfolioList && data.candidateBasicDetails.portfolioList.length > 1;
        break;
      case 1:
        isValid = validateAllFieldsFilled(data.candidateEducation);
        break;
      case 2:
        isValid = validateAllFieldsFilled(data.candidateSummaryEntity);
        break;
      case 3:
        isValid = validateAllFieldsFilled(data.candidateSkills) && data.candidateSkills.length > 0;
        break;
      case 4:
        isValid = validateAllFieldsFilled(data.candidateAchievements) && data.candidateAchievements.length > 0;
        break;
      case 5:
        isValid = validateAllFieldsFilled(data.candidateCommunications) && data.candidateCommunications.length > 0;
        break;
      case 6:
        isValid = validateAllFieldsFilled(data.candidateWorkExp) && data.candidateWorkExp.length > 0;
        break;
      default:
        isValid = false;
    }
    setIsStepValid(isValid);
  };

  const validateAllFieldsFilled = (obj) => {
    return Object.values(obj).every(value => value !== undefined && value !== null && value !== '');
  };

  const calculateHeights = (index) => {
    setCurrentStep(index);
    const heights = Array.from(containerRef.current.children).map(
      (child) => child.scrollHeight
    );
    setCardSectionHeights(heights);
  };

  const steps = [
    'Basic Details',
    'Education',
    'Summary',
    'Skills',
    'Achievements',
    'Communication',
    'Work Experience',
  ];

  return (
    <div className={classes.root}>
      <CssBaseline />
      <Container component={Paper} className={classes.container} ref={containerRef}>
        <Typography component="h1" variant="h4" gutterBottom>
          Create Resume
        </Typography>
        <Stepper activeStep={currentStep} alternativeLabel className={classes.stepper}>
          {steps.map((label) => (
            <Step key={label}>
              <StepLabel>{label}</StepLabel>
            </Step>
          ))}
        </Stepper>
        <SwipeableViews
          index={currentStep}
          onChangeIndex={calculateHeights}
          enableMouseEvents
          disabled={swipeDisabled}
          className={classes.form}
        >
          <Paper  className={classes.card} >
            <InsertBasicDetails onBasicDetailsChange={handleBasicDetailsChange} />
          </Paper>
          <Paper className={classes.card} >
            <InsertCandidateEducation onEducationChange={handleCandidateEducationChange} />
          </Paper>
          <Paper className={classes.card}>
            <InsertCandidateSummary onSummaryChange={handleCandidateSummaryChange} />
          </Paper>
          <Paper className={classes.card}>
            <InsertCandidateSkills onSkillsChange={handleSkillsChange} />
          </Paper>
          <Paper className={classes.card}>
            <InsertCandidateAchievements onAchievementsChange={handleCandidateAchievementsChange} />
          </Paper>
          <Paper className={classes.card}>
            <InsertCandidateCommunication onCommunicationChange={handleCommunicationChange} />
          </Paper>
          <Paper className={classes.card}>
            <InsertCandidateWorkExp onWorkExperienceChange={handleWorkExperienceChange} />
          </Paper>
        </SwipeableViews>
        
        <div className={classes.buttonContainer}>
          {currentStep > 0 && (
            <Button variant="contained" color="secondary" onClick={handleBack} className={classes.button}>
              Back
            </Button>
          )}
          {currentStep < steps.length - 1 && (
            <Button
              variant="contained"
              color="secondary"
              disabled={!isStepValid}
              onClick={handleNext}
              className={classes.button}
            >
              Next
            </Button>
          )}
          {currentStep === steps.length - 1 && (
            <Button variant="contained" color="primary" onClick={handleSubmit} className={classes.button}>
              Submit
            </Button>
          )}
        </div>
      </Container>
    </div>
  );
};

export default CreateResumePage;
