import CalendarMonthIcon from '@mui/icons-material/CalendarMonth';

const EducationSection = ({ data }) => {
    if (!data) {
        return null; // Handle case when education data is not available
    }

    const { degreeName, major, college, startYear, endYear } = data;

    const sectionStyle = {
        marginBottom: '20px'
    };

    const separatorStyle = {
        height: '1px',
        backgroundColor: 'black',
        margin: '10px 0'
    };

    const collegeStyle = {
        color: 'blue',
        fontWeight: 'bold',
        margin: '0', // Reduce margin to stick the elements together
    };

    const iconWithTextItemStyles = {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'start',
        marginLeft: '8px',
        margin: '0', // Reduce margin to stick the elements together
    };

    return (
        <div style={sectionStyle}>
            <h2>EDUCATION</h2>
            <div style={separatorStyle}></div>
            <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-start' }}>
                <p style={{ fontWeight: 'bold', margin: '0',marginBottom:'8px' }}>{degreeName}, {major}</p>
                <p style={{ ...collegeStyle, margin: '0', marginBottom:'8px' }}>{college}</p>
                <div style={{ ...iconWithTextItemStyles, margin: '0' }}>
                    <CalendarMonthIcon style={{ color: 'blue', marginRight: '8px', fontSize: 'small' }} />
                    <p style={{ margin: '0' }}>{startYear} - {endYear}</p>
                </div>
            </div>
        </div>
    );
};

export default EducationSection;
