// BasicDetailsModel.js

class BasicDetailsModel {
  constructor(legalName, emailId, phoneNumber, applicantDesignation, city, portfolioList) {
    this.legalName = legalName;
    this.emailId = emailId;
    this.phoneNumber = phoneNumber;
    this.applicantDesignation = applicantDesignation;
    this.city = city;
    this.portfolioList = portfolioList;
  }
}

export default BasicDetailsModel;
