import React from 'react';
import Chip from '@mui/material/Chip';
import Box from '@mui/material/Box';

// Define styles

const sectionStyle = {
    marginBottom: '20px'
  };

  const separatorStyle = {
    height: '1px',
    backgroundColor: 'black',
    margin: '10px 0'
  };


const chipStyles = {
  fontWeight: 'bold',
  borderWidth: '2px',
  borderColor: 'blue',
  flex: '0 0 10%', // Limit each chip to 30% of the container width 
};

const CandidateSkillsSection = (data) => {
  // Destructure data directly in the function parameters
  const  candidateSkills  = data.data || {}; // Use destructuring with default value to handle null data

  if (!candidateSkills || candidateSkills.length === 0) {
    return null; // or render a loading indicator, error message, etc.
  }

  return (
    <div style={sectionStyle}>
     <h2>SKILLS</h2>
        <div style={separatorStyle}></div>
    <div>    
    <Box  display="flex" flexWrap="wrap" gap={1} p={1}>
      {candidateSkills.map(skill => (
        <Chip
          key={skill.skillId}
          label={skill.skillName}
          variant="outlined"
          sx={chipStyles} // Apply styles from the defined constant
        />
      ))}
    </Box>
    </div>  
    </div>
  );
};

export default CandidateSkillsSection;
