import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import ReactGA from './ReactGAnalytics';
import { TextField, Button, Typography, List, ListItem, ListItemText, Box, Container, Paper, CssBaseline } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  container: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  paper: {
    marginTop: theme.spacing(4),
    padding: theme.spacing(3),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    backgroundColor: theme.palette.background.paper,
  },
  form: {
    width: '100%',
    marginTop: theme.spacing(3),
  },
  textField: {
    marginBottom: theme.spacing(2),
  },
  button: {
    margin: theme.spacing(1, 1, 1, 0),
    width:'100%',
    justifyContent:'center'
  
   
  },
  list: {
    width: '100%',
    backgroundColor: theme.palette.background.paper,
    marginBottom: theme.spacing(2),
  },
  listItemText: {
    textAlign: 'left',
  },
}));

const HomePage = () => {
  const classes = useStyles();
  const [resumeId, setResumeId] = useState('');
  const [isValidResumeId, setIsValidResumeId] = useState(true);
  const [resumeIdError, setResumeIdError] = useState('');
  const navigate = useNavigate();

  const validateResumeId = (value) => {
    const isValid = /^[a-zA-Z0-9]{8}$/.test(value);
    setIsValidResumeId(isValid);
    setResumeIdError(
      isValid
        ? ''
        : 'Resume ID must be exactly 8 characters long and contain only letters and numbers.'
    );
  };

  const handleResumeIdChange = (e) => {
    const { value } = e.target;
    setResumeId(value);
    validateResumeId(value);
  };

  const handleGetResume = (e) => {
    e.preventDefault();
    if (isValidResumeId && resumeId.trim()) {
      ReactGA.event({
        category: 'Button Click',
        action: 'Get Existing Resume Clicked',
        label: 'HomePage',
      });
      navigate('/resume', { state: { resumeId: resumeId.trim() } });
    }
  };

  const handleCreateResume = () => {
    if (isValidResumeId && resumeId.trim()) {
      navigate('/create-resume', { state: { resumeId: resumeId.trim() } });
      ReactGA.event({
        category: 'Button Click',
        action: 'Create Resume Clicked',
        label: 'HomePage',
      });
    }
  };

  useEffect(() => {
    ReactGA.send({ hitType: 'pageview', page: window.location.pathname, title: 'Page Viewed' });
  }, []);

  return (
    <Container  component="main" maxWidth="sm">
      <CssBaseline />
      <div className={classes.container}>
        <Typography component="h1" variant="h4" gutterBottom>
          Resume Builder
        </Typography>
        <Paper className={classes.paper} elevation={3}>
          <List className={classes.list}>
            <ListItem>
              <ListItemText className={classes.listItemText} primary="Ensure all the data you enter is accurate, as it cannot be edited once submitted." />
            </ListItem>
            <ListItem>
              <ListItemText className={classes.listItemText} primary="Remember to save your unique Resume ID for future reference." />
            </ListItem>
            <ListItem>
              <ListItemText className={classes.listItemText} primary="Your Resume ID must be a combination of letters and numbers (exactly 8 characters)." />
            </ListItem>
          </List>
          <form className={classes.form} onSubmit={handleGetResume}>
            <TextField
              variant="outlined"
              required
              fullWidth
              id="resumeId"
              label="Resume ID"
              name="resumeId"
              value={resumeId}
              inputProps={{ maxLength: 8 }} // Maximum character limit
              onChange={handleResumeIdChange}
              error={!isValidResumeId && resumeId !== ''}
              helperText={resumeIdError}
              className={classes.textField}
            />
            <Box display="flex" justifyContent="center">
              <Button
                type="submit"
                variant="contained"
                color="primary"
                className={classes.button}
                disabled={!isValidResumeId || !resumeId.trim()}
              >
                Get Existing Resume
              </Button>
              <Button
                onClick={handleCreateResume}
                variant="contained"
                color="secondary"
                className={classes.button}
                disabled={!isValidResumeId || !resumeId.trim()}
              >
                Create Resume
              </Button>
            </Box>
          </form>
        </Paper>
      </div>
    </Container>
  );
};

export default HomePage;
