import React from 'react';
import FiberManualRecordIcon from '@mui/icons-material/FiberManualRecord';

const CandidateCommsSection = (data) => {
    const languages = data.data|| []

    if(!languages || languages.length==0){
        return null
    }

    const separatorStyle = {
        height: '1px',
        backgroundColor: 'black',
        margin: '10px 0'
    };

    const sectionStyle = {
        marginBottom: '20px'
    };

    const languageStyle = {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        margin: '10px 0'
    };

    const proficiencyStyle = {
        display: 'flex',
        alignItems: 'center'
    };

    return (
        <div style={sectionStyle}>
            <h2>LANGUAGES</h2>
            <div style={separatorStyle}></div>
            {languages.map((language, index) => (
                <div key={index} style={languageStyle}>
                    <p style = {{margin: '0',marginBottom:'8px'}}>{language.languageName}</p>
                    <div style={proficiencyStyle}>
                        {[...Array(language.maxLevel)].map((_, i) => (
                            <FiberManualRecordIcon key={i} color={i < language.currentLevel ? 'primary' : 'disabled'} />
                        ))}
                        <span>{language.proficiency}</span>
                    </div>
                </div>
            ))}
        </div>
    );
};

export default CandidateCommsSection;
