import React from 'react';
import MyLocationIcon from '@mui/icons-material/MyLocation';
import CalendarMonthIcon from '@mui/icons-material/CalendarMonth';

const CandidateWorkExperience = (data) => {
    const candidateWorkEx = data.data || []
    if (!candidateWorkEx || candidateWorkEx.length === 0) {
        return null; // or render a loading indicator, error message, etc.
    }

    const sectionStyle = {
        marginBottom: '20px',
        margin: '0 20px'
    };

    const separatorStyle = {
        height: '1px',
        backgroundColor: 'black',
        margin: '10px 0'
    };

    const workExSeparatorStyle = {
       borderWidth:'0.5pxs',
       height:'0.5px',  
        backgroundColor:'grey',
        borderRadius : '0.5px',
        margin:'20px 0'
    }

    const companyDescStyle = {
        fontStyle: 'italic',
    };

    const bulletPointStyle = {
        marginLeft: '8px',
        textAlign: 'justify',
    };
    const iconWithTextItemStyles = {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'start',
       
    }

    const iconWithTextParentContainerStyle = {
        display: 'flex',
        flexDirection: 'row', // Align widgets vertically
        alignItems: 'center',
        justifyContent: 'start',
    }

    return (
        <div style={sectionStyle}>
            <h2>WORK EXPERIENCE</h2>
            <div style={separatorStyle}></div>
            {candidateWorkEx.map((workExp, index) => (
                <div key={index}>
                    <h2 style={{margin:'0',marginBottom:'8px'}}>{workExp.positionTitle}</h2>
                    <h3 style={{ color: 'blue',margin:'0', marginBottom:'8px' }}>{workExp.companyName}</h3>
                    <div style={iconWithTextParentContainerStyle}>
                        <div style={iconWithTextItemStyles}>
                            <CalendarMonthIcon style={{color:'blue', marginRight:'8px',marginBottom:'8px', fontSize:'small'}} />
                            <p style={{ fontWeight: 'bold' , margin:'0',marginBottom:'8px', }}>{workExp.startDate} - {workExp.endDate}</p>
                        </div>
                        <div style={iconWithTextItemStyles}>
                            <MyLocationIcon style={{color:'blue', marginLeft:'12px',marginRight:'8px',marginBottom:'8px',fontSize:'small'}}/>
                            <span style={{ fontWeight: 'bold' ,margin:'0', marginBottom:'8px'}}>| {workExp.companyCity} </span>
                        </div>
                    </div>
                    <p style={{margin:'0'}}>{workExp.companyDesc}</p>
                    <ul>
                        {workExp.bulletPoints.map((bulletPoint, index) => (
                            <li key={index} style={{ margin:'0'}} dangerouslySetInnerHTML={{ __html: bulletPoint }} />
                        ))}
                    </ul>
                    {index !== candidateWorkEx.length - 1 && <div style={workExSeparatorStyle}></div>} {/* Add separator if not the last item */} 

                </div>
            ))}
        </div>
    );
};

export default CandidateWorkExperience;
