

import React from 'react';
import AchievementIcon from '@mui/icons-material/EmojiEvents';
const CandidateAchievementSection = (data) => {

    const achievements = data.data || []

    if(!achievements || achievements.length==0){
        return null
    }

    const {achievementHeading,achievementDescription} = achievements[0]
    const separatorStyle = {
        height: '1px',
        backgroundColor: 'black',
        margin: '10px 0'
    };

    const sectionStyle = {
        marginBottom: '20px'
    };

    const achievementStyle = {
        display: 'flex',
        alignItems: 'center',
        margin: '10px 0'
    };

    const iconStyle = {
        marginRight: '10px',
        fontSize: '24px'
    };

    return (
        <div style={sectionStyle}>
            <h2>ACHIEVEMENTS</h2>
            <div style={separatorStyle}></div>
            <div style={achievementStyle}>
                <AchievementIcon style={{color:'blue',marginRight:'12px'}} />
                <h3 style={{margin:'0', marginBottom:'8px'}}>{achievementHeading} </h3>
            </div>
            <p style={{margin:'0',paddingLeft:'32px'}}>{achievementDescription}</p>
        </div>
    );
};

export default CandidateAchievementSection;
