import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import BasicDetailsSection from './components/showcaseDataSections/BasicDetailsSection';
import CandidateSkillsSection from './components/showcaseDataSections/CandidateSkillsSection';
import EducationSection from './components/showcaseDataSections/CandidateEducation';
import CandidateWorkExperience from './components/showcaseDataSections/CandidateWorkExperience';
import CandidateSummarySection from './components/showcaseDataSections/CandidateSummarySection';
import CandidateAchievementSection from './components/showcaseDataSections/CandidateAchievementSection';
import { useLocation , useNavigate } from 'react-router-dom';
import CandidateCommSkillSection from './components/showcaseDataSections/CandidateCommsSection';
import ReactGA from './ReactGAnalytics';
  const ResumePage = () => {
    const location = useLocation();
    const navigate = useNavigate();
  const { resumeId } = location.state;

  const [headerData, setHeaderData] = useState(null);
  const [dataCandidateSkills, setCandidateSkills] = useState([]);
  const [candidateSummary, setCandidateSummary] = useState(null);
  const [dataEducationSection, setEducationSection] = useState(null);
  const [dataWorkExperienceSection, setWorkExperience] = useState([]);
  const [candidateAchievements, setCandidateAchievements] = useState([]);
  const [candidateCommSkills, setCandidateCommSkills] = useState([]);

  // Refs for measuring column heights
  const leftColumnRef = useRef(null);
  const rightColumnRef = useRef(null);

  useEffect(() => {
    // Fetch data for header section
    ReactGA.send({ hitType: "pageview", page: window.location.pathname, title: "Page Viewed" });
    axios.get(`https://backendresume-xqwm.onrender.com/api/resume/${resumeId}`)
      .then(response => {
        setHeaderData(response.data.candidateBasicDetails);
        setCandidateSkills(response.data.candidateSkills);
        setEducationSection(response.data.candidateEducation);
        setWorkExperience(response.data.candidateWorkExp);
        setCandidateSummary(response.data.candidateSummaryEntity);
        setCandidateAchievements(response.data.candidateAchievements);
        setCandidateCommSkills(response.data.candidateCommunications);
      })
      .catch(error => {
        console.error('Error fetching resume data:', error);
        alert('Resume ID does not exist. Redirecting back...');
        navigate(-1);
      });
  }, [resumeId,navigate]);

  

  
  // Inline CSS constants
  const parentSectionStyle = {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'stretch', // Stretch to take full width
    background: 'linear-gradient(to bottom , #e6e6e6, #f2f2f2)',
    boxShadow: '0 4px 8px rgba(0, 0, 0, 0.1)',
    margin: 'auto', // Center the content horizontally
    boxSizing: 'border-box', // Include padding and border in total width/height
  };

  const mainContentStyle = {
    display: 'flex',
    justifyContent: 'center',
    marginTop: '20px',
    fontFamily: 'calibri' /* Set your preferred font stack here */
  };

  const leftColumnStyle = {
    flex: '0.65',
    marginRight: '20px',
  };

  const rightColumnStyle = {
    flex: '0.35',
    flexDirection: 'column',
    marginLeft: '20px',
    marginRight: '20px'
  };

  return (
    <div style={parentSectionStyle}>
      <BasicDetailsSection data={headerData} />
      <div style={mainContentStyle}>
        <div ref={leftColumnRef} style={leftColumnStyle}>
          <CandidateSummarySection data={candidateSummary} />
          <CandidateWorkExperience data={dataWorkExperienceSection} />
        </div>
        <div ref={rightColumnRef} style={rightColumnStyle}>
          <CandidateSkillsSection data={dataCandidateSkills} />
          <EducationSection data={dataEducationSection} />
          <CandidateAchievementSection data={candidateAchievements} />
          <CandidateCommSkillSection data={candidateCommSkills} />
        </div>
      </div>
    </div>
  );
};

export default ResumePage;
