import React, { useState } from 'react';
import { TextField, Card, CardContent, Typography, makeStyles } from '@material-ui/core';
import BasicDetailsModel from '../../dataObjects/BasicDetailsModel'; // Importing the model class

const useStyles = makeStyles((theme) => ({
  card: {
    // margin: theme.spacing(2),
    // padding: theme.spacing(2),
  },
  textField: {
    marginBottom: theme.spacing(2),
  },
}));

const InsertBasicDetails = ({ onBasicDetailsChange }) => {
  const classes = useStyles();
  const [basicDetails, setBasicDetails] = useState(new BasicDetailsModel('', '', '', '', '', [])); // Add the portfolioList field
  const [errors, setErrors] = useState({});

  const handleChange = (event, fieldName) => {
    const { value } = event.target;
  
    // Initialize errorMessage
    let errorMessage = '';
  
    // Validation logic based on field name
    switch (fieldName) {
      case 'phoneNumber':
        // Validate phone number
        const phoneNumberPattern = /^\d{10}$/;
        if (!phoneNumberPattern.test(value)) {
          errorMessage = 'Phone number must be 10 digits';
        }
        break;
      case 'dateOfBirth':
        // Add your date validation logic here if needed
        break;
      default:
        // Check if the fieldName corresponds to a portfolio list item
        if (fieldName.startsWith('portfolioList[')) {
          const index = parseInt(fieldName.split('[')[1].split(']')[0]);
          // Update the value in the portfolioList array
          const updatedPortfolioList = [...basicDetails.portfolioList];
          updatedPortfolioList[index] = value;
          // Update basic details state
          const updatedBasicDetails = { ...basicDetails, portfolioList: updatedPortfolioList };
          setBasicDetails(updatedBasicDetails);
          // Propagate the changes to the parent component
          onBasicDetailsChange(updatedBasicDetails);
          return; // Exit early to avoid updating other fields
        }
        break;
    }
  
    // Update errors state if there's a validation error
    setErrors((prevErrors) => ({
      ...prevErrors,
      [fieldName]: errorMessage,
    }));
  
    // Update basic details state for fields other than portfolioList
    const updatedBasicDetails = { ...basicDetails, [fieldName]: value };
    setBasicDetails(updatedBasicDetails);
  
    // Propagate the changes to the parent component for fields other than portfolioList
    onBasicDetailsChange(updatedBasicDetails);
  };
  
  
  return (
    <Card className={classes.card}>
      <CardContent  >
        <Typography variant="h5" gutterBottom>Basic Details</Typography>
        <TextField
          className={classes.textField}
          label="Legal Name"
          value={basicDetails.legalName}
          onChange={(e) => handleChange(e, 'legalName')}
          variant="outlined"
          fullWidth
        />
        <TextField
          className={classes.textField}
          label="Email"
          value={basicDetails.emailId}
          onChange={(e) => handleChange(e, 'emailId')}
          variant="outlined"
          fullWidth
        />
        <TextField
          className={classes.textField}
          label="Phone Number"
          value={basicDetails.phoneNumber}
          onChange={(e) => handleChange(e, 'phoneNumber')}
          variant="outlined"
          fullWidth
          inputProps={{ maxLength: 10 }} // Maximum character limit
          type="tel"
          error={!!errors.phoneNumber}
          helperText={errors.phoneNumber}
        />
        <TextField
          className={classes.textField}
          label="Applicant Designation"
          value={basicDetails.applicantDesignation}
          onChange={(e) => handleChange(e, 'applicantDesignation')}
          variant="outlined"
          fullWidth
        />
        <TextField
          className={classes.textField}
          label="City"
          value={basicDetails.city}
          onChange={(e) => handleChange(e, 'city')}
          variant="outlined"
          fullWidth
        />
        {/* Add input fields for LinkedIn and GitHub IDs */}
        <TextField
          className={classes.textField}
          label="LinkedIn ID"
          value={basicDetails.portfolioList[0] || ''}
          onChange={(e) => handleChange(e, 'portfolioList[0]')} // Update portfolioList[0]
          variant="outlined"
          fullWidth
        />
        <TextField
          className={classes.textField}
          label="GitHub ID"
          value={basicDetails.portfolioList[1] || ''}
          onChange={(e) => handleChange(e, 'portfolioList[1]')} // Update portfolioList[1]
          variant="outlined"
          fullWidth
        />
      </CardContent>
    </Card>
  );
};

export default InsertBasicDetails;
