// InsertCandidateEducation.js

import React, { useState } from 'react';
import { TextField, Card, CardContent, Typography, makeStyles } from '@material-ui/core';
import CandidateEducationModel from '../../dataObjects/CandidateEducationModel.js';

const useStyles = makeStyles((theme) => ({
  card: {
    // margin: theme.spacing(2),
    // padding: theme.spacing(2),
  },
  textField: {
    marginBottom: theme.spacing(2),
  },
}));

const InsertCandidateEducation = ({ onEducationChange }) => {
  const classes = useStyles();
  const [education, setEducation] = useState(new CandidateEducationModel('', '', '', '', ''));

  const handleChange = (field, value) => {
    setEducation((prevEducation) => ({
      ...prevEducation,
      [field]: value,
    }));
  };

  // Handle change for each input field
  const handleStartYearChange = (event) => handleChange('startYear', event.target.value);
  const handleEndYearChange = (event) => handleChange('endYear', event.target.value);
  const handleDegreeNameChange = (event) => handleChange('degreeName', event.target.value);
  const handleMajorChange = (event) => handleChange('major', event.target.value);
  const handleCollegeChange = (event) => handleChange('college', event.target.value);

  // Notify parent component of the changes
  React.useEffect(() => {
    onEducationChange(education);
  }, [education, onEducationChange]);

  return (
    <Card className={classes.card}>
      <CardContent>
        <Typography variant="h5" gutterBottom>Education</Typography>
        <TextField
          className={classes.textField}
          label="Start Year"
          value={education.startYear}
          onChange={handleStartYearChange}
          variant="outlined"
          fullWidth
          placeholder="MM/YYYY"
        />
        <TextField
          className={classes.textField}
          label="End Year"
          value={education.endYear}
          onChange={handleEndYearChange}
          variant="outlined"
          fullWidth
          placeholder="MM/YYYY"
        />
        <TextField
          className={classes.textField}
          label="Degree Name"
          value={education.degreeName}
          onChange={handleDegreeNameChange}
          variant="outlined"
          fullWidth
        />
        <TextField
          className={classes.textField}
          label="Major"
          value={education.major}
          onChange={handleMajorChange}
          variant="outlined"
          fullWidth
        />
        <TextField
          className={classes.textField}
          label="College"
          value={education.college}
          onChange={handleCollegeChange}
          variant="outlined"
          fullWidth
        />
      </CardContent>
    </Card>
  );
};

export default InsertCandidateEducation;
    