import React, { useState } from 'react';
import { TextField, Button, Card, CardContent, Typography, IconButton, makeStyles } from '@material-ui/core';
import CloseIcon from '@mui/icons-material/Close';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import ExpandLessIcon from '@mui/icons-material/ExpandLess';

// Define styles for the expand/collapse button
const useStyles = makeStyles((theme) => ({
  card: {
    marginBottom:theme.spacing(2),
    borderRadius: theme.shape.borderRadius * 2,
    boxShadow: theme.shadows[3],
   
  },
  expandButton: {
    marginLeft: 'auto',
  },
  addButton: {
    marginTop:'8px',
    borderRadius: theme.shape.borderRadius * 2,
    marginRight: theme.spacing(2),
    background: 'linear-gradient(to right, #64b5f6, #1976d2)',
    '&:hover': {
      background: 'linear-gradient(to right, #1976d2, #64b5f6)',
    },
  },
  removeButton: {
    borderRadius: theme.shape.borderRadius * 2,
    background: 'linear-gradient(to right, #ef5350, #c62828)',
    '&:hover': {
      background: 'linear-gradient(to right, #c62828, #ef5350)',
    },
  },
  bulletPointContainer: {
    display: 'flex',
    alignItems: 'center',
    marginTop: theme.spacing(1),
  },
}));


const InsertCandidateWorkExp = ({ onWorkExperienceChange }) => {
  const [workExperiences, setWorkExperiences] = useState([]);
  const classes = useStyles();

  const handleAddWorkExperience = () => {
    const updatedWorkExperiences = workExperiences.map((exp) => ({
      ...exp,
      expanded: false,
    }));
    const newWorkExperience = {
      companyName: '',
      startDate: '',
      endDate: '',
      companyDesc: '',
      bulletPoints: [''],
      companyCity: '',
      positionTitle: '',
      expanded: true,
    };
    setWorkExperiences([...updatedWorkExperiences, newWorkExperience]);
  };

  const handleToggleExpansion = (index) => {
    const updatedWorkExperiences = [...workExperiences];
    updatedWorkExperiences.forEach((exp, i) => {
      exp.expanded = i === index ? !exp.expanded : false;
    });
    setWorkExperiences(updatedWorkExperiences);
  };

  const handleWorkExperienceChange = (index, field, value) => {
    const updatedWorkExperiences = [...workExperiences];
    updatedWorkExperiences[index][field] = value;
    setWorkExperiences(updatedWorkExperiences);
    onWorkExperienceChange(updatedWorkExperiences);
  };

  const handleAddBulletPoint = (index) => {
    const updatedWorkExperiences = [...workExperiences];
    updatedWorkExperiences[index].bulletPoints.push('');
    setWorkExperiences(updatedWorkExperiences);
    onWorkExperienceChange(updatedWorkExperiences);
  };

  const handleRemoveBulletPoint = (index, bpIndex) => {
    const updatedWorkExperiences = [...workExperiences];
    updatedWorkExperiences[index].bulletPoints.splice(bpIndex, 1);
    setWorkExperiences(updatedWorkExperiences);
    onWorkExperienceChange(updatedWorkExperiences);
  };

  const handleRemoveWorkExperience = (index) => {
    const updatedWorkExperiences = workExperiences.filter((exp, i) => i !== index);
    setWorkExperiences(updatedWorkExperiences);
    onWorkExperienceChange(updatedWorkExperiences);
  };

  const handleBulletPointChange = (workExpIndex, bpIndex, value) => {
    const updatedWorkExperiences = [...workExperiences];
    updatedWorkExperiences[workExpIndex].bulletPoints[bpIndex] = value;
    setWorkExperiences(updatedWorkExperiences);
    onWorkExperienceChange(updatedWorkExperiences);
  };
  return (
    <div>
      {workExperiences.length === 0 && (
      <Card className={classes.card}>
      <CardContent>
        <Typography variant="h5" gutterBottom>No work experiences added yet.</Typography>
        <Button className={classes.addButton} variant="contained" color="primary" onClick={handleAddWorkExperience}>
          Add Work Experience
        </Button>
      </CardContent>
    </Card>
      )}
      {workExperiences.map((workExperience, index) => (
        <Card key={index} className={classes.card}>
          <CardContent>
            <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
              <Typography variant="h5">Work Experience {index + 1}</Typography>
              <IconButton
                className={classes.expandButton}
                onClick={() => handleToggleExpansion(index)}
              >
                {workExperience.expanded ? <ExpandLessIcon /> : <ExpandMoreIcon />}
              </IconButton>
            </div>
            {workExperience.expanded && (
              <>
                <TextField
                  label="Company Name"
                  value={workExperience.companyName || ''}
                  onChange={(e) => handleWorkExperienceChange(index, 'companyName', e.target.value)}
                  variant="outlined"
                  fullWidth
                  style={{ marginBottom: '16px' }}
                />
                <TextField
                  label="Start Date"
                  value={workExperience.startDate || ''}
                  onChange={(e) => handleWorkExperienceChange(index, 'startDate', e.target.value)}
                  variant="outlined"
                  fullWidth
                  style={{ marginBottom: '16px' }}
                  placeholder="MM/YYYY"
                />
                <TextField
                  label="End Date"
                  value={workExperience.endDate || ''}
                  onChange={(e) => handleWorkExperienceChange(index, 'endDate', e.target.value)}
                  variant="outlined"
                  fullWidth
                  style={{ marginBottom: '16px' }}
                  placeholder="MM/YYYY"
                />
                <TextField
                  label="Position Title"
                  value={workExperience.positionTitle || ''}
                  onChange={(e) => handleWorkExperienceChange(index, 'positionTitle', e.target.value)}
                  variant="outlined"
                  fullWidth
                  style={{ marginBottom: '16px' }}
                />
                <TextField
                  label="Company Description"
                  value={workExperience.companyDesc || ''}
                  onChange={(e) => handleWorkExperienceChange(index, 'companyDesc', e.target.value)}
                  variant="outlined"
                  fullWidth
                  style={{ marginBottom: '16px' }}
                />
                <TextField
                  label="Company City"
                  value={workExperience.companyCity || ''}
                  onChange={(e) => handleWorkExperienceChange(index, 'companyCity', e.target.value)}
                  variant="outlined"
                  fullWidth
                  style={{ marginBottom: '16px' }}
                />
                <Button  variant="outlined" color="primary" onClick={() => handleAddBulletPoint(index)}>
                  Add Bullet Point
                </Button>
                {workExperience.bulletPoints && workExperience.bulletPoints.map((bp, bpIndex) => (
                  <div key={bpIndex} className={classes.bulletPointContainer}>
                    <TextField
                      label={`Bullet Point ${bpIndex + 1}`}
                      value={bp || ''}
                      onChange={(e) => handleBulletPointChange(index, bpIndex, e.target.value)}
                      variant="outlined"
                      fullWidth
                      style={{ marginRight: '8px' }}
                    />
                    <IconButton onClick={() => handleRemoveBulletPoint(index, bpIndex)}><CloseIcon /></IconButton>
                  </div>
                ))}
              </>

            )}
          </CardContent>
          <div style={{ display: 'flex', justifyContent: 'center', margin:'10px', flexDirection:'row'}}>
          <Button className={classes.addButton} variant="contained" color="primary" onClick={handleAddWorkExperience}>
        Add Work Experience
      </Button>
            <Button className={classes.removeButton}  variant='contained' color='primary' onClick={() => handleRemoveWorkExperience(index)}>
              Remove
            </Button>
        
          </div>
        </Card>
      ))}
      {/* <Button className={classes.addButton} variant="contained" color="primary" onClick={handleAddWorkExperience}>
        Add Work Experience
      </Button> */}
    </div>
  );
};

export default InsertCandidateWorkExp;
