// HeaderSection.js
import React from 'react';


const HeaderSection = ({ data }) => { // Destructure data directly from props
   
    
    // Check if data is null or undefined
    if (!data) {
        return null; // or return a loading indicator or default text
    }
    const headerSectionStyles = {
        flex:'display',
        flexDirection: 'column', // Align widgets vertically
        alignItems: 'start', // Align widgets in the center horizontally
        color: 'white', // Add a color to the text to make it visible
        margin: '0 20px',
    };
    
    const { legalName, applicantDesignation } = data;

    return (
        <div style={headerSectionStyles}>
            <h1 style={{fontSize:'48px', margin:'0', marginBottom:'8px'}}>{legalName}</h1>
            <h3 style={{color: 'white',margin:'0'}}>{applicantDesignation}</h3> 
        </div>
    );
};

export default HeaderSection;
