import React from 'react';
import HeaderSection from './HeaderSection';
import ContactSection from './ContactSection';
import ExtraPortfolioDetailsSection from './ExtraPortfolioSection';

const BasicDetailsSection = ({data}) => {

    if (!data) {
        return null; // or return a loading indicator or default text
    }
    
    const BasicDetailsSectionStyle = {
       paddingBottom:'40px',
       paddingTop:'40px', 
       marginBottom:'20px' ,
       background: 'linear-gradient(to right, #2c3e50, #4a235a)', // Gradient colors
    }

    return (
       <div className='basic-details-section' style={BasicDetailsSectionStyle}>
        <HeaderSection data={data} />
        <ContactSection data= {data}/>
        <ExtraPortfolioDetailsSection data={data} />
        </div>
    );
};

export default BasicDetailsSection;