

const CandidateSummarySection = ({data}) => {

    if(!data){
        return null
    }

    const sectionStyle = {
        marginBottom: '20px',
        margin:'0 20px 0 20px'
      };
    
      const separatorStyle = {
        height: '1px',
        backgroundColor: 'black',
        margin: '10px 0'
      };

    const parentSummaryStyle = {
        flexDirection: 'column', // Align widgets vertically
        alignItems: 'start', // Align widgets in the center horizontally
        color: 'black', // Add a color to the text to make it visible
        margin: '0px 0px 0px 0px',
    }


    const {summary} = data

    return (
        <div style={sectionStyle}>
        <h2>SUMMARY</h2>
           <div style={separatorStyle}></div>
            <div style={parentSummaryStyle}>
                <span style={{textAlign : 'justify'}} dangerouslySetInnerHTML={{ __html: summary }}></span>
            </div>
       </div>    
    
    );



}
export default CandidateSummarySection;