// InsertCandidateSummary.js

import React, { useState } from 'react';
import { TextField, Card, CardContent, Typography, makeStyles } from '@material-ui/core';
import CandidateSummaryModel from '../../dataObjects/CandidateSummaryModel.js';

const useStyles = makeStyles((theme) => ({
  card: {
    // margin: theme.spacing(2),
    // padding: theme.spacing(2),
  },
  textField: {
    marginBottom: theme.spacing(2),
  },
}));

const InsertCandidateSummary = ({ onSummaryChange }) => {
  const classes = useStyles();
  const [summary, setSummary] = useState(new CandidateSummaryModel(''));

  const handleChange = (event) => {
    const updatedSummary = new CandidateSummaryModel(event.target.value);
    setSummary(updatedSummary);
    onSummaryChange(updatedSummary); // Propagate the changes to the parent component
  };

  return (
    <Card className={classes.card}>
      <CardContent>
        <Typography variant="h5" gutterBottom>Candidate Summary</Typography>
        <TextField 
          className={classes.textField}
          label="Summary"
          inputProps={{ maxLength: 999 }} // Maximum character limit
          value={summary.summary}
          onChange={handleChange}
          variant="outlined"
          fullWidth
          multiline
          rows={4}
        />
      </CardContent>
    </Card>
  );
};

export default InsertCandidateSummary;
