// InsertCandidateCommunication.js
import React, { useState } from 'react';
import { TextField, Button, Card, CardContent, Typography } from '@material-ui/core';
import CandidateCommunicationModel from '../../dataObjects/CandidateCommunicationModel.js';

const InsertCandidateCommunication = ({ onCommunicationChange }) => {
  const [communications, setCommunications] = useState([]);

  const handleAddCommunication = () => {
    const newCommunication = new CandidateCommunicationModel('', 0,5);
    setCommunications([...communications, newCommunication]);
  };

  const handleRemoveCommunication = (index) => {
    const updatedCommunications = [...communications];
    updatedCommunications.splice(index, 1);
    setCommunications(updatedCommunications);
    onCommunicationChange(updatedCommunications);
  };

  const handleCommunicationChange = (index, field, value) => {
    const updatedCommunications = [...communications];
    updatedCommunications[index][field] = value;
    updatedCommunications[index]['maxLevel']=5
    setCommunications(updatedCommunications);
    onCommunicationChange(updatedCommunications);
  };

  return (
    <Card style={{ marginBottom: '16px' }}>
      <CardContent>
        <Typography variant="h5" gutterBottom>Communication Skills</Typography>
        {communications.map((communication, index) => (
          <div key={index} style={{ marginBottom: '16px', display: 'flex', alignItems: 'center' }}>
            <TextField
              label="Language"
              value={communication.languageName}
              onChange={(e) => handleCommunicationChange(index, 'languageName', e.target.value)}
              variant="outlined"
              style={{ marginRight: '16px' }}
            />
            <TextField
              label="Current Level/5"
              value={communication.currentLevel}
              onChange={(e) => handleCommunicationChange(index, 'currentLevel', e.target.value)}
              variant="outlined"
              type="number"
              contentEditable='false'
              InputProps={{ inputProps: { min: 0, max: 5 } }}
              style={{ marginRight: '16px', width: '100px' }}
            />
            <Button variant="outlined" color="secondary" onClick={() => handleRemoveCommunication(index)}>Remove</Button>
          </div>
        ))}
        <Button variant="contained" color="primary" onClick={handleAddCommunication}>Add Language</Button>
      </CardContent>
    </Card>
  );
};

export default InsertCandidateCommunication;
